import React from 'react'
import { graphql } from 'gatsby'
import { Flex, Box } from 'grid-styled'
import styled, { css } from 'styled-components'
import Img from 'gatsby-image'
import FlickrHero from 'react-flickr-hero'

import { media } from '../utils/style'

import Layout from '../components/layout'
import NavBar from '../components/navbar'
import HeroText from '../components/heroText'
import SocialIcons from '../components/socialIcons'
import Portfolio from '../components/portfolio'
import Showcase from '../components/showcase'

const Content = styled.div`
  & > a {
    visibility: hidden;
    display: block;
    height: 0;
  }
  & > h1 {
    text-align: center;
  }
`

const Title = styled.h1`
  font-family: 'Raleway';
  text-transform: uppercase;
  letter-spacing: 6px;
  margin-bottom: 40px;
  font-weight: 400;
  font-size: 32px;
  line-height: 40px;
  border: none;
  color: #292929;

  ${props =>
    props.small &&
    css`
      font-size: 12px;
      letter-spacing: 2px;
      font-weight: 700;
      line-height: 24px;
    `}
`

const Section = styled.div`
  text-align: center;
  padding-top: 45px;
  padding-bottom: 40px;

  a {
    font-family: 'Lato';
  }

  p {
    margin-bottom: 64px;
    font-size: large;
    color: #666;
  }

  h1,
  h2,
  h3,
  h4,
  h5,
  h6 {
    font-family: 'Raleway';
    text-transform: uppercase;
    color: #292929;
  }

  h4 {
    letter-spacing: 3px;
    font-weight: 400;
    font-size: 24px;
    line-height: 32px;
    color: #292929;
  }

  span {
    color: #666;
    opacity: 0.5;
    display: block;
  }

  & > div:last-child {
    border-bottom: none !important;
  }

  ${props =>
    props.center &&
    css`
      text-align: left;
      & > * {
        margin-left: 30vw;
      }
      h4 {
        margin-left: 20vw;
      }

      ${media.xs`
        & > div {
          margin-left: 3vw !important;
        }
    `}
    `}

  ${props =>
    props.dark &&
    css`
      background: #292929;
      * {
        color: #eee;
      }
      span {
        text-align: left;
        font-size: 16px;
        line-height: 28px;
        font-weight: 400;
        opacity: 0.5;
      }
      span,
      p {
        color: #fefefe !important;
      }
      h6 {
        color: #fff;
        font-weight: 700;
      }
      h4 {
        color: #fff;
      }
      div {
        border-bottom: 1px solid #333 !important;
      }
    `}
`

const Item = styled.div`
  width: 40%;
  margin: 0 auto;
  border: none;
  border-bottom: 1px solid #eee;
  h6 {
    letter-spacing: 2px;
    font-weight: 700;
    padding-top: 6px;
  }
  span,
  p {
    font-size: 13px;
    line-height: 24px;
    color: #666;
  }
  span {
    opacity: 0.75;
    float: right;
    text-transform: uppercase;
  }
  p {
    margin-bottom: 24px;
    opacity: 0.5;
  }
  ${media.xs`
    width: 90%;

  `}
`

export default props => {
  const content = (
    <Content>
      <FlickrHero
        api_key="ea44b91986d3c86cd9be779c9d140bde"
        user_id="189488346@N05"
        album_id="72157715282810012"
        fillPage
      />
      <HeroText />
      <SocialIcons
        style={{
          position: 'absolute',
          margin: '0 auto',
          left: 0,
          right: 0,
          bottom: 16,
        }}
        icons={[
          {
            name: 'facebook',
            href: 'https://www.facebook.com/adrianfact',
          },
          {
            name: 'instagram',
            href: 'https://www.instagram.com/drnhrtnt_/',
          },
          {
            name: 'whatsapp',
            href: 'https://api.whatsapp.com/send?phone=6281292767383',
          },
        ]}
      />
      <a id="about-me">Tentangku</a>
      <Section>
        <Title>Tentangku</Title>
        <Flex alignItems="center" flexDirection="column">
          <Box px={2} width={[1, 1 / 2]}>
            <p>
              Saat ini diriku sedang bekerja dalam naungan{' '}
              <a href="https://910sportswear.com">910 Sportswear</a> dalam bidang Web Maintenance.
              Selama perjalanan karir-ku jarangnya memiliki masalah pekerjaan terkhususkan untuk mempelajari ilmu baru.
              Dalam hidupku aku memiliki pengalaman dalam menjadi pemimpin dalam Adr.team dan beberapa Video Director untuk beberapa promosi game Nintendo Switch.
              Aku tidak akan menutupi sifat asli sebab itu aku menaruh beberapa sosial media-ku di web ini.
              
            </p>
          </Box>
          <Box px={2} width={180}>
            <Img
              sizes={
                props.data.allFile
                  ? props.data.allFile.edges[0].node.childImageSharp.sizes
                  : {}
              }
            />
          </Box>
        </Flex>
      </Section>
      <Title small>Sejarah karir</Title>
      <a id="portfolio">Pengalaman</a>
      <Portfolio items={props.data.allMarkdownRemark.edges} />
      <a id="experience">Pengalaman</a>
      <Section center dark>
        <h4>Pengalaman</h4>
        <span>Tempatku berkarir.</span>
        <Item>
          <span>16 JULI 2020 - SEKARANG</span>
          <h6>910 Sportswear</h6>
          <p>Web Maintenancer</p>
        </Item>
        <Item>
          <span>1 JULI 2020 - SEKARANG</span>
          <h6>ENERGI OF ASIA ( NOIA )</h6>
          <p>Content Creator Lead</p>
        </Item>
        <Item>
          <span>19 JAN 2020 - SEKARANG</span>
          <h6>Adr.team</h6>
          <p>Founder</p>
        </Item>
        <Item>
          <span>1 DES 2018 - 15 SEPT 2019</span>
          <h6>ZROAR Team</h6>
          <p>Founder</p>
        </Item>
      </Section>
      <a id="tech">Teknologi</a>
      <Section center>
        <h4>Teknologi</h4>
        <span>Teknologi yang ku nikmati dalam hidupku.</span>
        <Showcase
          images={
            props.data.allImageSharp ? props.data.allImageSharp.edges : []
          }
        />
      </Section>
      <a id="education">Pendidikan</a>
      <Section dark center>
        <h4>PENDIDIKAN</h4>
        <span>Pendidikanku yang telah ditempuh dan sedang di tempuh.</span>
        <Item>
          <span>2018 - SEKARANG</span>
          <h6>Jenjang Perkuliahan</h6>
          <p>Areta Informatics College</p>
        </Item>
        <Item>
          <span>2015 - 2018</span>
          <h6>Sekolah Menengah Kejuruan ( TKJ )</h6>
          <p>SMK Islamic Village</p>
        </Item>
        <Item>
          <span>2012 - 2015</span>
          <h6>Sekolah Menengah Pertama</h6>
          <p>SMP Pribadi 1 Tangerang</p>
        </Item>
        <Item>
          <span>2006 - 2012</span>
          <h6>Sekolah Dasar</h6>
          <p>SDN Cihuni 2 Tangerang</p>
        </Item>
      </Section>
      <a id="honoursAndAwards">Sertifikat</a>
      <Section center>
        <h4>SERTIFIKAT</h4>
        <span> Sertifikat dalam perjalanan karir.</span>
        <Item>
          <span>2016</span>
          <h6>MTCNA</h6>
          <p>Sertifikasi MikRotik</p>
        </Item>
      </Section>
    </Content>
  )
  return (
    <Layout location={props.location}>
      <NavBar main children={content.props.children} />
      {content}
    </Layout>
  )
}

export const pageQuery = graphql`
  query IndexQuery {
    allMarkdownRemark(sort: { order: DESC, fields: [frontmatter___date] }) {
      edges {
        node {
          timeToRead
          excerpt(pruneLength: 120)
          fields {
            slug
          }
          frontmatter {
            date(formatString: "MMMM DD, YYYY")
            title
            tags
            image {
              childImageSharp {
                sizes(
                  maxWidth: 500
                  duotone: {
                    highlight: "#333333"
                    shadow: "#111111"
                    opacity: 65
                  }
                  ) {
                  ...GatsbyImageSharpSizes
                }
              }
            }
          }
        }
      }
    }
    allImageSharp: allFile(filter: { relativePath: { regex: "/logos/" } }) {
      edges {
        node {
          id
          childImageSharp {
            sizes(maxWidth: 300, grayscale: true) {
              ...GatsbyImageSharpSizes_tracedSVG
            }
          }
        }
      }
    }
    allFile(filter: { name: { regex: "/signature/" } }) {
      edges {
        node {
          childImageSharp {
            sizes(maxWidth: 200, grayscale: true) {
              ...GatsbyImageSharpSizes_tracedSVG
            }
          }
        }
      }
    }
  }
`
